import { Grid } from "./Grid";
import { ColConstraint, GridConstraints } from "./IGridCell";

export enum GridWorkerMessageTypes {
  GRID_CREATE,
  GRID_JOB,

  JOB_DONE,
  CELL_RECT_UPDATE,
}

interface GridMessage<T extends GridWorkerMessageTypes> {
  type: T;
  jobId: number;
  id: number;
}
export interface CreateGridMessage extends GridMessage<GridWorkerMessageTypes.GRID_CREATE> {
  grid: [
    constrain: GridConstraints,
    constraintValue: ColConstraint,
  ]
}

type GridFunctionName = 'push' | 'toArray';

export interface GridJobMessage extends GridMessage<GridWorkerMessageTypes.GRID_JOB> {
  fnName: GridFunctionName;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  args: any[]
}
interface WorkerRect extends Float64Array {
  length: 4;
}
export interface CellRectUpdateMessage extends GridMessage<GridWorkerMessageTypes.CELL_RECT_UPDATE> {
  rect: WorkerRect
  key: unknown
  // rect: Float64Array
}
export interface GridJobDoneMessage extends GridMessage<GridWorkerMessageTypes.JOB_DONE> {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  results: any[];
  object: Grid<unknown>
}
