/* eslint-disable prefer-spread */
/* eslint-disable no-restricted-globals */

import { Grid } from "./Grid";
import {
  CreateGridMessage,
  GridJobDoneMessage,
  GridJobMessage,
  GridWorkerMessageTypes,
  CellRectUpdateMessage,
} from './WorkerTypes';

// eslint-disable-next-line @typescript-eslint/no-explicit-any
const context: Worker = self as any;

console.log('Grid Worker Loaded');
const _cache = new Map<number, Grid<unknown>>();
context.addEventListener('message', e => {
  const data = e.data;
  switch (data.type) {
  case GridWorkerMessageTypes.GRID_CREATE: {
    const { id, grid: gridArgs } = data as CreateGridMessage;
    _cache.set(id, new Grid<unknown>(...gridArgs));
    _cache.get(id)?.subscribeGlobal((rect, key) => {
      const rectBuffer = new Float64Array(rect);
      return context.postMessage({
        id,
        type: GridWorkerMessageTypes.CELL_RECT_UPDATE,
        rect: rectBuffer,
        key,
      } as CellRectUpdateMessage, [rectBuffer.buffer]);
    });
    break;
  }
  case GridWorkerMessageTypes.GRID_JOB: {
    const { id, fnName, args, jobId } = data as GridJobMessage;
    const grid = _cache.get(id);
    grid && grid[fnName] && grid[fnName] instanceof Function && setTimeout(() => {
      // @ts-expect-error fnName
      const results = [].concat(grid[fnName].apply(grid, args));
      context.postMessage({
        type: GridWorkerMessageTypes.JOB_DONE,
        id,
        jobId,
        results,
        object: grid,
      } as GridJobDoneMessage);

    });
    break;
  }
  default:
    break;
  }
});

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export default null as any;
